html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

body {
    margin: 0;
    font-family: "asd", sans-serif;
    background-color: $white;
}

img {
    max-width: 100%;
}

a {
    text-decoration: none;
    color: inherit;
    display: inline-block;
}

.list-reset {
    list-style: none;
    padding: 0;
    margin: 0;
}

.is-outer {
    position: relative !important; // stylelint-disable-line declaration-no-important
    left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
    overflow: hidden;
}

.is-hidden {
    display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
    display: none;

    @include mobile {
        display: block;
    }
}

.error {
    border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.js-focus-visible :focus:not(.focus-visible) {
    outline: none;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);
}

.btn-reset {
    border: none;
    background: transparent;
    cursor: pointer;
    padding: 0;
}

.btn {
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

.container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1200px;
}

.title {}
